import React, { Component } from 'react';
import Layout from '../components/Layout'
import algoliasearch from 'algoliasearch/lite';
import { InstantSearch, SearchBox, Hits, Highlight } from 'react-instantsearch-dom';
import Helmet from 'react-helmet'

const Hit = ({ hit }) => 
<article>
{/* <div>{hit.frontmatter.title}</div>
    <Highlight attribute="name" hit={hit}/>
    <a href={hit.fields.slug}>{hit.excerpt}</a> */}
    <p>
      <Highlight attribute="frontmatter.title" 
       tagName="strong"
      hit={hit}/>
    </p>
    <p>
      <Highlight attribute="excerpt"
       tagName="strong"
      hit={hit}/>
    </p>
  </article>

const searchClient = algoliasearch(
  '8FW8QFPWQ4',
  'cefb20681c67524242483bb6a55cf2fc'
);

class searchingPage extends Component {
    render() {
            const { title } = this.props.data.site.siteMetadata
            return (
              <div>
                <Layout>
                <div>
                  <Helmet title={`${title} ::search`} />
                  <InstantSearch
                    indexName="mspace_index"
                    searchClient={searchClient}
                  >
                    <SearchBox />
                    <Hits hitComponent={Hit}/>
                </InstantSearch>
                </div>
              </Layout>
              </div>
            );
   }
}

export default searchingPage;


export const query = graphql`
  query {
    site{
      siteMetadata{
        title
        description
      }
    }
}
`